"use client";

import React from "react";
import { motion } from "framer-motion";
import { FaHeartbeat, FaUserMd, FaHospitalAlt, FaAmbulance, FaBrain, FaXRay, FaChild, FaBone, FaHandHoldingHeart, FaBaby } from "react-icons/fa";

export default function AboutPage() {
  return (
    <main className="max-w-6xl mx-auto px-4 py-10">
      <motion.h1
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        className="text-4xl font-bold mb-6 text-blue-800"
      >
        O nas
      </motion.h1>

      <motion.section
        className="mb-14 bg-white p-6 rounded-xl shadow-lg"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        <div className="grid md:grid-cols-2 gap-8">
          <div>
            <p className="text-lg text-gray-700 mb-4">
              <strong>Miejski Szpital Ogólny</strong> to więcej niż placówka medyczna — to miejsce, gdzie technologia spotyka się z troską o człowieka. Od 1985 roku nieprzerwanie dążymy do doskonałości, oferując kompleksową opiekę zdrowotną na najwyższym poziomie.
            </p>
            <p className="text-lg text-gray-700">
              Nasz zespół to lekarze z pasją, pielęgniarki z sercem i personel administracyjny, który dba o każdy szczegół, by pacjent czuł się bezpiecznie i komfortowo.
            </p>
          </div>
          <div className="flex items-center justify-center">
            <img
              src="data:image/jpeg;base64,"
              alt="Zespół szpitala"
              className="rounded-xl shadow-xl w-full h-auto object-cover"
            />
          </div>
        </div>
      </motion.section>

      <motion.section
        className="mb-14"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        <h2 className="text-2xl font-semibold mb-4 text-blue-700">Dlaczego warto wybrać nasz szpital?</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6">
          <div className="p-5 bg-gray-100 rounded-xl shadow hover:scale-105 transition-transform">
            <FaUserMd className="text-3xl text-blue-600 mb-3" />
            <h3 className="font-semibold text-lg">Doświadczeni specjaliści</h3>
            <p className="text-gray-600 text-sm">Nasz personel to liderzy w swoich dziedzinach medycyny.</p>
          </div>
          <div className="p-5 bg-gray-100 rounded-xl shadow hover:scale-105 transition-transform">
            <FaHospitalAlt className="text-3xl text-green-600 mb-3" />
            <h3 className="font-semibold text-lg">Nowoczesny sprzęt</h3>
            <p className="text-gray-600 text-sm">Inwestujemy w technologię, by oferować najwyższy standard usług.</p>
          </div>
          <div className="p-5 bg-gray-100 rounded-xl shadow hover:scale-105 transition-transform">
            <FaAmbulance className="text-3xl text-red-600 mb-3" />
            <h3 className="font-semibold text-lg">Ratownictwo 24/7</h3>
            <p className="text-gray-600 text-sm">Zawsze gotowi do działania w nagłych przypadkach.</p>
          </div>
          <div className="p-5 bg-gray-100 rounded-xl shadow hover:scale-105 transition-transform">
            <FaHeartbeat className="text-3xl text-pink-500 mb-3" />
            <h3 className="font-semibold text-lg">Troskliwa opieka</h3>
            <p className="text-gray-600 text-sm">Każdy pacjent jest dla nas ważny — stawiamy na empatię i indywidualne podejście.</p>
          </div>
        </div>
      </motion.section>

      <motion.section
        className="mb-14"
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6 }}
      >
        <h2 className="text-2xl font-semibold mb-4 text-blue-700">Nasze oddziały</h2>
        <p className="text-gray-700 mb-2">Specjalizujemy się w szerokim zakresie dziedzin medycznych:</p>
        <ul className="list-none text-gray-700 space-y-2">
          <li className="flex items-center gap-2"><FaHeartbeat className="text-red-500" /> Kardiologia</li>
          <li className="flex items-center gap-2"><FaBrain className="text-purple-600" /> Neurologia</li>
          <li className="flex items-center gap-2"><FaXRay className="text-blue-500" /> Chirurgia ogólna</li>
          <li className="flex items-center gap-2"><FaChild className="text-yellow-500" /> Pediatria</li>
          <li className="flex items-center gap-2"><FaBone className="text-green-600" /> Ortopedia</li>
          <li className="flex items-center gap-2"><FaHandHoldingHeart className="text-pink-500" /> Dermatologia</li>
          <li className="flex items-center gap-2"><FaBaby className="text-indigo-500" /> Ginekologia i położnictwo</li>
        </ul>
      </motion.section>

      <motion.section
        className="bg-red-50 p-6 rounded-xl shadow-inner hover:scale-105 transition-transform duration-700"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        transition={{ duration: 0.6 }}
      >
        <div className="flex items-center gap-3 mb-2">
          <FaAmbulance className="text-3xl text-red-600" />
          <h2 className="text-2xl font-semibold text-red-700">Nagłe przypadki</h2>
        </div>
        <p className="text-gray-700 mb-2">
          Nasz oddział ratunkowy działa <strong>24 godziny na dobę, 7 dni w tygodniu</strong>. Doświadczony zespół lekarzy i ratowników medycznych
          jest zawsze gotowy, aby zapewnić szybką i skuteczną pomoc w każdej sytuacji kryzysowej.
        </p>
        <p className="text-xl font-bold text-red-600">📞 Telefon alarmowy: +48 123 456 789</p>
      </motion.section>
    </main>
  );
}